
.ratings-wrapper{
	position: absolute;
	top: 174px;
	left: 0;
	bottom: 0;
	width: 100%;
	overflow: hidden;
}
.ratings-wrapper .info{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex
}
.ratings-wrapper .ratings-content .info .score{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 138px;
	        flex: 0 0 138px;
	margin: 18px 0;
	text-align: center;
	border-right: 1px solid #eee;
}
.ratings-wrapper .ratings-content .info .score .num{
	font-weight: 500;
	font-size: 24px;
	color: #f90;
	line-height: 28px;
}
.ratings-wrapper .ratings-content .info .score .text,.contrast{
	text-align: center;
	font-size: 12px;
	line-height: 24px;
	color: #07111b;
}
.ratings-wrapper .ratings-content .info .service{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	padding: 18px 24px;
	font-size: 0px;
}
.ratings-wrapper .ratings-content .info .service .serviceScore,.foodScore{
	margin-bottom: 8px;
	font-size: 0px;
}
.ratings-wrapper .ratings-content .info .service  .text{
	font-size: 12px;
	color: #07111b;
	line-height: 18px;
	margin-right: 12px;
}
.ratings-wrapper .ratings-content .info .service .num{
	font-size: 12px;
	color: #f90;
	padding-left: 12px;
	line-height: 18px
}
.ratings-wrapper .ratings-content .info .service .star{
	display: inline-block;
	vertical-align: top;
}
.ratings-wrapper .ratings-content .info .service .deliveryTime .time{
	font-size: 12px;
	color: #93999f;
	line-height: 18px;
}
@media screen and (max-width:320px) {
.ratings-wrapper .ratings-content .info .score{
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 120px;
		        flex: 0 0 120px;
		width: 120px;
}
.ratings-wrapper .ratings-content .info .service .num{
		padding-left: 6px;
}
.ratings-wrapper .ratings-content .info .service{
		padding-left: 6px;
}
}
.ratings-wrapper .divider{
	width: 100%;
	height: 16px;
	background: #f3f5f7;
	border-bottom: 1px solid rgba(7, 17, 27, .1);
	border-top: 1px solid rgba(7, 17, 27, .1);
}



