
.goods{
	position: absolute;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	top: 174px;
	bottom: 46px;
	overflow: hidden;
}
.goods .menu-wrapper{
	display: inline-block;
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 80px;
	        flex: 0 0 80px;
	width: 80px;
	background: #f3f5f7;
	margin-top: 2px;
}
.goods .menu-wrapper .menu-item{
	width: 56px;
	height: 54px;
	line-height: 14px;
	display: table;
	padding: 0 12px;
}
.goods .menu-wrapper .menu-item .icon{
	display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin-right: 3px;
	background-size: 12px 12px !important;
	background-repeat: no-repeat;
}
.icon.special{
	background: url('special_3@2x.png');
}
.icon.discount{
	background: url('discount_3@2x.png');
}
.goods .menu-wrapper .menu-item .text{
	display: table-cell;
	width: 56px;
	vertical-align: middle;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	font-size: 12px;
}
.goods .foods-wrapper{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	margin-top: 2px;
}
.goods .foods-wrapper .food-list .title{
	height: 26px;
	line-height: 26px;
	font-size: 12px;
	color: #93999f;
	padding-left: 12px;
	border-left: 1px solid #d9dde1;
	background: #f3f5f7;
}
.goods .foods-wrapper .food-list .food-item{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 0 18px;
	padding: 18px 0;
	border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.goods .foods-wrapper .food-list .food-item .icon{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 57px;
	        flex: 0 0 57px;
}
.goods .foods-wrapper .food-list .food-item .content{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	padding-left: 10px;
}
.goods .foods-wrapper .food-list .food-item .content h2{
	font-size: 14px;
	height: 14px;
	line-height: 14px;
	font-weight: 700;
	color: #000;
	margin: 2px 0 8px 0;
}
.goods .foods-wrapper .food-list .food-item .content .desc{
	font-size: 10px;
	line-height: 12px;
	margin-bottom: 8px;
	color: #93999f;
}
.goods .foods-wrapper .food-list .food-item .content .sellCount{
	font-size: 10px;
	color: #93999f;
	line-height: 12px;
}
.goods .foods-wrapper .food-list .food-item .content .price{
	font-size: 14px;
	height: 24px;
	line-height: 24px;
	color: #f00;
}
.goods .foods-wrapper .food-list .food-item .content .price .oldPrice{
	color: #ccc;
	padding-left: 4px;
	text-decoration: line-through;
	font-size: 10px;
}



